  }
  .preloader__sector:nth-child(30) {
    transform: rotateY(348deg) translateZ(7rem);
  }
  
  /* Animations */
  @keyframes tiltSpin {
    from {
      transform: rotateY(0) rotateX(30deg);
    }
    to {
      transform: rotateY(1turn) rotateX(30deg);
    }
  }
  @keyframes spin {
    from {
      transform: rotateY(0);
    }
    to {
      transform: rotateY(1turn);
    }
  }

  .half-1,
  .half-1 .container > .row {
  
    min-height: 600px;
  }
  
  @media (max-width: 991.98px) {
    .half-1 .bg {
      height: 200px;
    }
  }
  
  .half-1 .contents {
    background: #130720;
    color: #fff;
    
    font-size: 20px;
  }
  .half-1 .contents {
    width: 80%;
  }
  
  .half-1 .bg {
    width: 20%;
  }
  
  @media (min-width: 990px) {
    .half-1 content {
      height: 100px !important;
    }
  }
  
  @media (max-width: 991.98px) {
    .half-1 .contents,
    .half-1 .bg {
      width: 100%;
    }
  }
  
  .half-1 .contents .form-control,
  .half-1 .bg .form-control {
    border: none;
  
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    height: 54px;
    color: rgb(85, 78, 78);
    background: rgb(208, 204, 204);
  }
  .half-1 .contents .form-control:active,
  .half-1 .contents .form-control:focus,
  .half-1 .bg .form-control:active,
  .half-1 .bg .form-control:focus {
    outline: none;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1);
  }
  
  .half-1 .bg {
    background-size: cover;
    background-position: center;
  }
  
  .half-1 a {
    color: #888;
    text-decoration: underline;
  }
  
  .half-1 .btn {
    height: 54px;
    padding-left: 30px;
    padding-right: 30px;
  }
  
  .half-1 .forgot-pass {
    position: relative;
    top: 2px;
    font-size: 14px;
  }